export default {
	data() {
		return {
			userinfo:[],




			isH5Plus:false,

			orderTypeLise:[
				//name-标题 icon-图标 badge-角标
				{name:'全部订单',icon:'l4.png',badge:19},
				{name:'待付款',icon:'l1.png',badge:1},
				{name:'待发货',icon:'l2.png',badge:2},
				{name:'待收货',icon:'l3.png',badge:6},
				{name:'售后订单',icon:'l5.png',badge:0}
			],
			severList:[
				[
					{name:'库存管理',icon:'skv.png'},
					{name:'售后申请',icon:'sellafter.png'},
					{name:'我要上货',icon:'upgood.png'},
					{name:'反馈意见',icon:'comment.png'}
				],
				[
					{name:'历史记录',icon:'history.png'},
					{name:'联系客服',icon:'kefu.png'},
					{name:'关于平台',icon:'about.png'}
				]
			]
		}
	},
	onLoad() {
		//加载
		this.init();
	},
	onReady() {
			let t=this;
			uni.login({
				success:function(e){
					t.getapiinfo(e.code)
				}
			})
	},
	methods: {
		async getapiinfo(code){
			let info= await this.$apis.chencklogin({code:code});
			let t=this;
			if(info.code==0){
				uni.reLaunch(
				{url:"../../pages/auth/auth"}
				)
			}else{
				this.userinfo=info.data;
				uni.setStorageSync('openid',info.data.openid)
				console.log(this.userinfo,'已登录')
			}
		},
		init() {
			//用户信息
			// this.userinfo={
			// 	face:'../../static/HM-PersonalCenter/face.jpeg',
			// 	username:"VIP会员10240",
			// 	integral:"1435"
			// }		
		},
		//用户点击订单类型
		toOrderType(index){
			//uni.showToast({title: this.orderTypeLise[index].name});
			uni.navigateTo({url:'./orderlist/orderlist?tbIndex='+index}) 
		},
		//用户点击列表项
		toPage(list_i,li_i){
			uni.showToast({title: this.severList[list_i][li_i].name});
		}
	}
}
